<template>
  <div id="app">
    <!-- 租用确认模态窗口 -->
    <div v-if="showRentModal" class="modal">
      <div class="modal-content">
        <span class="close" @click="closeRentModal">&times;</span>
        <p>你确定要租用这辆车吗？</p>
        <button @click="confirmRent">确认</button>
        <button @click="closeRentModal">取消</button>
      </div>
    </div>
    <div class="son">
      <!-- 搜索框 -->
      <input type="text" v-model="searchQuery" placeholder="搜索..." />
      <button @click="search">搜索</button>
      <button style="background-color:rgb(0, 255, 191);margin-left:700px" @click="add()">
        <i class="fa fa-plus" aria-hidden="true" />租出车辆
      </button>
      <!-- 表格 -->
      <table>
        <thead>
          <tr>
            <th></th>
            <th>序号</th>
            <th>车辆名称</th>
            <th>租赁价格</th>
            <th>车辆类型</th>
            <th>车辆描述</th>
            <th>车主</th>
            <th>车辆状态</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in originalResults" :key="index">
            <td><input type="checkbox" /></td>
            <td>{{ index + 1 }}</td>
            <td>{{ item.name }}</td>
            <td>{{ item.price }}</td>
            <td>{{ item.type }}</td>
            <td>{{ item.description }}</td>
            <td>{{ item.owner }}</td>
            <td>{{ item.status === 'AVAILABLE' ? '未租出' : '已租出' }}</td>
            <td>
              <button class="zy" @click="editItem(index)" :disabled="item.status !== 'AVAILABLE'">{{ item.status ===
                'AVAILABLE' ? '租用' : '已租出' }}</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
    
<script>
import axios from 'axios';
export default {
  data() {
    return {
      userName: "",
      userID: "",
      searchQuery: "",
      originalResults: [],
      showRentModal: false,
      rentIndex: null
    };
  },
  created() {
    this.userName = localStorage.getItem('userName');
    this.userID = localStorage.getItem('userID');
    console.log('userID:' + this.userID)

    this.updateData();


  },
  methods: {
    add() {
      this.$router.push('/main/addcar')
    },
    updateData() {
      axios.get('/car/getAllCars')
        .then(response => {
          this.originalResults = response.data.map(car => ({
            id: car.carID,
            name: car.carName,
            price: car.carPrice,
            type: car.carType,
            description: car.carDis,
            owner: car.carMaster,
            status: car.carState,
            renter: car.renter
          }));
          console.log(this.originalResults)
        })
        .catch(error => {
          console.error(error);
          console.log("error")
        });
    },

    search() {
      if (this.searchQuery) {
        this.originalResults = this.originalResults.filter(item =>
          item.name.includes(this.searchQuery) ||
          item.price.toString().includes(this.searchQuery) ||
          item.type.includes(this.searchQuery) ||
          item.description.includes(this.searchQuery)
        );
      } else {
        this.updateData();
      }
    },

    editItem(index) {
      this.rentIndex = index;
      this.showRentModal = true;
    },
    closeRentModal() {
      this.showRentModal = false;
    },
    confirmRent() {
      let url = `/car/updateCarRanter/${this.originalResults[this.rentIndex].id}/${this.userID}`;

      axios.put(url)
        .then(response => {
          console.log('租用成功: ', response.data);
          // 在这里添加租用成功后的逻辑
          this.showRentModal = false;
          updateData();
          alert("租用成功！")
        })
        .catch(error => {
          console.error('租用失败: ', error);
          // 在这里添加租用失败后的逻辑
        });
    }
  },
};
</script>
  
<style scoped>
.son {
  width: 100%;
  background-color: white;
}

input[type="text"] {
  margin-top: 1vw;
  margin-left: 2vw;
  margin-bottom: 1vw;
  padding: 10px;
  font-size: 16px;
  width: 300px;
}

button {
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
  color: white;
  background-color: #6891e2;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.zy {
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
  color: white;
  background-color: green;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

table {
  margin-left: 2vw;
  width: 95%;
  border-collapse: collapse;
  margin-top: 20px;
  border-radius: 5px;
}

th,
td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

th {
  background-color: #007bff;
  color: white;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

/* 模态窗口样式 */
.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}

.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}
</style>